html {
  font-size: 100%;
}

:root {
  --dark_cyan: #19647E;
  --orange: #F23F23;
  --grey: #B8B8B8;
  --white: #F7F6F6;
  --darkest: #BFBBFB;
  --black: #100c08;
  --lowOpac: #BFBBFB;
  --jetcolor: #3333335d;
  --smokyblack: #333333;
  --egblac: #005B96;
  --whites: #FFF5EE;
  --light_orange: #f23f23a4;
  --crimson: #FF0066;
}


body,
#root {
  width: 100vw;
  height: auto;
  background-color: var(--black);
  overflow-x: hidden;

}

#root::-webkit-scrollbar,
body::-webkit-scrollbar {
  display: none;
  -ms-overflow-style: none;

}


@media screen and (min-device-width: 320px) and (max-device-width: 480px) {
  html {
    font-size: 60%;
  }
}


@media screen and (min-device-width: 481px) and (max-device-width: 649px) {
  html {
      font-size: 60%;
    }
}


@media screen and (min-device-width: 650px) and (max-device-width: 768px) {
  html {
    font-size:60%;
  }
}

@media screen and (min-device-width: 769px) and (max-device-width: 1024px) {
  html {
    font-size: 70%;
  }
}


@media screen and (min-device-width: 1025px) and (max-device-width: 1200px) {
  html {
    font-size: 90%;
  }
}