#about {
    margin-top: 5rem;
    width: 100%;
    height: auto;
    background-color: var(--black);
    position: relative;
    z-index: 1;
    margin-bottom: 30vh;

}

.HeadP {
    font-size: 2rem;
    font-family: 'Russo One', sans-serif;
    padding: 1rem 2rem;
    color: var(--orange);
    margin: 0 auto;
    text-align: center;
    position: relative;
}

.HeadP::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 40%;
    width: 0rem;
    height: 0.1rem;
    background-color: var(--orange);
    animation: startenim infinite alternate 2s ease;
}

@keyframes startenim {

    100% {
        transform: translateX(3rem);
        width: 12rem;
    }
}

.main-box img {
    position: absolute;
    top: 13%;
    left: 2%;
    width: 30rem;
    height: auto;
    animation: shake infinite alternate-reverse 2s ease-in-out;
}

.aboutbox {

    width: 50%;
    height: auto;
    /* transform: translate(100%, 20%); */
    font-size: 1.1rem;
    color: var(--white);
    line-height: 2.2rem;
    font-family: 'Prompt', sans-serif;
    text-align: justify;
    margin-left: 45%;
    margin-top: 5%;

}

.aboutbox h2 {
    margin-bottom: 2rem;
    font-family: 'Josefin Sans', sans-serif;
}

@keyframes shake {
    100% {
        transform: translate(5%, 5%);
    }
}

#circle {

    position: absolute;
    transform: translate(-20%, -20%);
    width: 55rem;
    height: 60rem;
    z-index: -1;
}

@media screen and (min-device-width: 481px) and (max-device-width: 649px) {
    .main-box img {
        opacity: 10%;
    }

    .aboutbox {

        width: 100%;
        margin-left: 0%;
        margin-top: 7rem;
        padding: 0rem 3rem;

    }

    .HeadP::before {
        left: 0%;
    }

    @keyframes startenim {

        100% {
            transform: translateX(100%);
            width: 100%;
        }
    }

}

@media screen and (min-device-width: 320px) and (max-device-width: 480px) {
    .main-box img {
        opacity: 10%;
    }

    .aboutbox {

        width: 100%;
        margin-left: 0%;
        margin-top: 7rem;
        padding: 0rem 3rem;

    }

    .HeadP::before {
        left: 0%;
    }

    @keyframes startenim {

        100% {
            transform: translateX(100%);
            width: 100%;
        }
    }
}