#footer,
.footer {
    width: 100%;
    height: 20rem;
    background-color: var(--jetcolor);
}

.name-logo {

    text-align: center;
    font-size: 1.5rem;
    font-family: 'Russo One', sans-serif;
    font-weight: 300;
    color: var(--white);
}

.social {
    width: 100%;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 1rem;

}

.name-logo span {
    color: var(--orange);
}

.fa-circle-arrow-up {
    font-size: 2rem;
    color: var(--white);
    cursor: pointer;
}


.social-links {
    width: 100%;
    height: 4rem;
    margin: 1rem 0rem;

}

.social-links ul {
    width: 30%;
    height: 100%;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 0 auto;
    text-align: center;

}

.social-links a {

    font-size: 1rem;
    color: var(--white);
    font-family: 'Prompt', sans-serif;
    text-decoration: none;

}

.middle-sec-foot {
    width: 30%;
    text-align: center;
    font-size: 1.2rem;
    font-family: 'Capriola', sans-serif;
    color: var(--white);
    border-bottom: 0.01rem solid var(--white);
    padding: 1rem 0rem;
    margin: 0 auto;
    line-height: 1.8rem;
}

.slink {
    width: 1.8rem;
    height: auto;
}

.copy-right {
    font-size: 0.8rem;
    font-family: 'Source Code Pro', monospace;
    color: var(--white);
    padding: 1rem;
    width: 100%;
    background-color: var(--orange);
    text-align: center;
}



@media screen and (min-device-width: 1025px) and (max-device-width: 1100px) {
    .social-links ul {
        width: 40%;
    }
}

@media screen and (min-device-width: 650px) and (max-device-width: 768px) {
    .social-links ul {
        width: 35%;
    }
}

@media screen and (min-device-width: 481px) and (max-device-width: 595px) {
    .social-links ul {
        width: 50%;
    }
}

@media screen and (min-device-width: 595px) and (max-device-width: 649px) {
    .social-links ul {
        width: 45%;
    }
}

@media screen and (min-device-width: 320px) and (max-device-width: 370px) {
    .social-links ul {
        width: 80%;
    }

    .middle-sec-foot {
        width: 80%;
    }

}

@media screen and (min-device-width: 371px) and (max-device-width:458px) {
    .social-links ul {
        width: 65%;
    }

    .middle-sec-foot {
        width: 80%;
    }

}

@media screen and (min-device-width: 459px) and (max-device-width:480px) {
    .social-links ul {
        width: 60%;
    }

    .middle-sec-foot {
        width: 80%;
    }

}