#home {
    position: relative;
    z-index: 2;
    width: 100%;
    height: auto;
    width: 100%;

}

.container {
    width: 80%;
    height: 33rem;
    margin: 0 auto;
    background-image: url('./back.png');
    display: flex;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;

}

.first {
    width: 50%;
    height: 100%;
    background-color: var(--egblac);
    opacity: 20%;
}

.Second {
    width: 50%;
    height: 100%;
    background-color: var(--orange);
    opacity: 20%;
}

.inside-box {
    position: absolute;
    color: var(--white);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 90%;
    height: 90%;
    text-align: center;
    border-radius: 2%/4%;
    border: 0.2rem solid var(--white);
}

.text-writting h1 {
    font-size: 5rem;
    font-family: 'Russo One', sans-serif;
    margin-bottom: 2%;
    background: linear-gradient(to right, var(--orange) 50%, var(--dark_cyan) 50%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}


.text-writting h4 {
    font-size: 2rem;
    font-family: 'Merriweather Sans', sans-serif;
    margin: 1% 0%;
}

.links-icons {
    width: 100vw;
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 1rem;
}


.SocialLinks {
    position: relative;
    width: 100%;
    list-style: none;
    height: 100%;
}

.SocialLinks,
.SocialLinks li {
    width: 10rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 0.3rem;
}

.SocialLinks li {
    height: 2.5rem;
    border: 0.1rem solid var(--white);
    border-radius: 1%/8%;
}


.SocialLinks li a {
    width: 10rem;
    text-align: center;
    color: var(--white);
    font-size: 1rem;
    text-decoration: none;
    font-family: 'Josefin Sans', sans-serif;

}

.SocialLinks li:hover {
    transition: all 0.5s ease-in-out;
    border: 0;
}

.SocialLinks li:hover:nth-child(even) {
    background-color: var(--orange);
}

.SocialLinks li:hover:nth-child(odd) {
    background-color: var(--dark_cyan);
}

.SocialLinks li:hover a {
    transform: scale(1.2);
    color: var(--white);
    transition: all 0.5s ease;
}


.text-writting {
    margin-top: 18%;
}

.text-writting P {
    width: 75%;
    margin: 0 auto;
    font-size: 1rem;
    line-height: 1.5rem;
    font-family: 'Raleway', sans-serif;
}

.button {
    background-color: transparent;
    padding: 0.5rem 1rem;
    margin: 1rem;
    border: 0.1rem solid var(--white);
    border-radius: 2%/10%;
    cursor: pointer;
    color: var(--white);
    font-family: 'Prompt', sans-serif;
    font-size: 1rem;
}


.btn2:hover {
    background-color: var(--dark_cyan);
    border: 0px;
}

.btn1:hover {
    background-color: var(--orange);
    border: 0px;
}

@media screen and (min-device-width: 481px) and (max-device-width: 649px) {
    .SocialLinks {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }
}

@media screen and (min-device-width: 320px) and (max-device-width: 410px) {
    .SocialLinks {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }

    .container {

        height: 39rem;

    }

    .inside-box {
        border-radius: 1rem;
    }
}

@media screen and (min-device-width: 411px) and (max-device-width: 456px) {
    .SocialLinks {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }

    .container {
        height: 37rem;
    }

    .inside-box {
        border-radius: 1rem;
    }
}

@media screen and (min-device-width: 457px) and (max-device-width: 480px) {
    .SocialLinks {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }

    .container {
        height: 30rem;
    }

    .inside-box {
        border-radius: 1rem;
    }
}