#articles {
    width: 100%;
    height: 30rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    /* flex-wrap: wrap; */

}

tr,
td,
th {

    border: 0.1rem solid var(--white);
}

th {
    background-color: var(--dark_cyan);
}

th,
td {
    padding: 0.5rem 2rem;
}

td a {

    text-decoration: none;
    color: var(--white);
}

.linkbox {
    background-color: var(--jetcolor);
}

.artcilesHead {
    width: 100%;
    height: auto;
    font-size: 2rem;
    font-family: 'Russo One', sans-serif;
    color: var(--orange);
    text-align: center;
    position: relative;

}

.ArticlesTable {
    font-size: 1.2rem;
    font-family: 'Prompt', sans-serif;

}

.Abtn {

    padding: 0.5rem 2rem;
    border: 0.1rem solid var(--white);
    border-radius: 2%/10%;
    cursor: pointer;
    color: var(--white);
    font-family: 'Prompt', sans-serif;
    font-size: 1rem;
    background-color: var(--black);
    text-decoration: none;

}

.Abtn:hover {
    background-color: var(--light_orange);
    transition: all 0.5s ease-in-out;
}

@media screen and (min-device-width: 481px) and (max-device-width: 649px) {
    #articles {
        height: 40rem;
    }
}

@media screen and (min-device-width: 320px) and (max-device-width: 480px) {
    #articles {
        height: 50rem;
    }
}