#skills {
    width: 100%;
    height: 60rem;
    background-color: var(--black);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.head-sec {

    font-size: 2rem;
    font-family: 'Russo One', sans-serif;
    color: var(--orange);
}



.img-head {

    width: 35rem;
    height: 6rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 4rem;
}

.container-section {
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.skill-card {
    width: 25rem;
    height: 13rem;
    padding: 2rem;
    background-color: var(--jetcolor);
    color: var(--white);
    border-radius: 2%;
    line-height: 1.5rem;
    margin: 0rem 0.2rem;

}

.skill-card:hover {
    cursor: pointer;

}

.image-card,
.head-section,
.description {
    width: 100%;
}

.Tech_Image {
    width: 5rem;
    height: auto;
}

.image-card,
.head-section,
.description-skills {
    text-align: center;
}

.head-section {
    font-size: 1.2rem;
    font-family: 'Russo One', sans-serif;
    color: var(--white);
}

.Small-font-set {
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Merriweather Sans', sans-serif;
    margin-top: 0.4rem;
}

.Card_Image {
    width: 4rem;
    height: auto;
}

.description-skills p {
    font-size: 0.8rem;
    font-family: 'Josefin Slab', serif;
    color: var(--white);

}

.Edit-Color {
    color: var(--orange);
}

@media screen and (min-device-width: 481px) and (max-device-width: 609px) {
    #skills {
        height: 120rem;
    }
}

@media screen and (min-device-width: 320px) and (max-device-width: 480px) {
    #skills {
        height: 115rem;
    }

    .img-head {
        width: 100%;
        justify-content: space-evenly;
    }

    .Tech_Image {
        display: none;
    }
}