.Main-text {
    color: var(--white);
    font-family: 'Cookie', cursive;
    font-size: 2rem;
}

.Main-text::first-letter {
    color: var(--orange);
}

#nav,
#nav .InitialList {
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
}

#nav {
    font-family: 'Capriola', sans-serif;
    width: 80%;
    height: 5rem;
    font-size: 1rem;
    margin: 0 auto;
    color: var(--white);

}

#nav .InitialList {
    width: 50%;
    height: 100%;
    list-style: none;
    position: relative;

}

#nav .InitialList .litem {
    position: relative;
    z-index: 1;
}

.Link {
    cursor: pointer;
}

.Link::before {
    content: '';
    position: absolute;
    top: 120%;
    right: 50%;
    transform: translate(50%, -50%);
    width: 0rem;
    height: 0.2rem;
    transition: 0.5s ease-in-out;
    z-index: -1;
    background-color: var(--orange);
}

.Link:hover::before {
    width: 5rem;
}

.fa-bars {
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
}

.topNavSidebar {
    width: 100%;
    padding: 1rem;
    background-color: var(--black);
    position: absolute;
    left: 50%;
    transform: translate(-50%, 95%);
    z-index: 10;
    border-radius: 1%/10%;

}

.ListItemsNavSide {
    display: flex;
    width: 100%;
    padding: 1rem 0rem;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
    list-style: none;

}

.newli {
    font-size: 1rem;
    font-family: 'Capriola', sans-serif;
    cursor: pointer;
}

@media screen and (min-device-width: 481px) and (max-device-width: 649px) {
    #nav .InitialList {
        width: 80%;
    }
}

@media screen and (min-device-width: 320px) and (max-device-width: 480px) {

    #nav .InitialList {
        display: none;
    }

    .fa-bars {

        display: block;
    }
}